import { uniPopup } from '@dcloudio/uni-ui';
import amap from '../../../sdk/amap-wx.js';
import gcoord from '../../../sdk/gcoord.js';
export default {
	components: {
		uniPopup
	},
	data() {
		return {
			addressName: '定位中...',

			indicatorDots:true,			// 显示轮播指示点
			autoplay: true,				// 自动轮播
            interval: 3000,				// 轮播等待时间
            duration: 500,				// 滑动动画时长
            circular: true,				// 是否衔接滑动
			
			amapPlugin: null, 			// new地图
			key: '1ef4d7504a4fe4ecd24b24ec78c976fb',
			address: '',				// 详细地址
			lat:'',						// 纬度
			lng:'',						// 经度
		};
	},
	onLoad() {
		// 如果Storage存在数据,就不调用接口
		uni.getStorage({
		    key: 'city',
		    success: function (res) {
				if(!res.data){
					// this.getCity()
				}
		    }
		});
	},
	mounted() {
		this.amapPlugin = new amap.AMapWX({
			//初始化
			key: this.key
		});
		//获取当前位置
		// this.amapPlugin.getRegeo({
		// 	success: (res) => {
		// 		this.address = res[0].regeocodeData.formatted_address;
		// 		this.lat = res[0].latitude;
		// 		this.lng = res[0].longitude;
		// 		console.log(this.address);//可自己查看所需返回值取用
		// 	}
		// });
	},
	methods: {
		// 获取城市列表
		getCity(getData){
			uni.request({
				url: this.$lib + '/city',
				data: getData,
				method:'GET',//请求方式  或GET
				success: res => {
					if(!getData){
						uni.setStorage({
							key: 'city',
							data: JSON.stringify(res.data),
							success: function () {
								console.log('success');
							}
						})
					}
				}
			});
		},
		// 位置授权
		getAuthorizeInfo(){
			const that = this;
			uni.authorize({
				scope: 'scope.userLocation',
				success() { // 允许授权
					that.getLocationInfo();
				},
				fail(){    // 拒绝授权
					that.openConfirm();
					console.log("你拒绝了授权，无法获得周边信息")
				}
			})
		},
		// 获取地理位置
		getLocationInfo(){
			var that = this;
			// uni.getLocation({
			// 	type: 'wgs84',
			// 	success (res) {
			// 		console.log(res)
			// 		console.log('当前位置的经度：' + res.longitude);
			// 		console.log('当前位置的纬度：' + res.latitude);
			// 	}
			// });
			uni.showLoading({
				title: '获取信息中'  
			});  
			this.amapPlugin.getRegeo({  
				success: (data) => {  
					console.log(data)  
					this.addressName = data[0].name || '定位失败';  
					uni.hideLoading();  
				}  
			}); 
		},
		// 再次获取授权
		// 当用户第一次拒绝后再次请求授权
		openConfirm(){
			uni.showModal({
				title: '请求授权当前位置',
				content: '需要获取您的地理位置，请确认授权',
				success: (res)=> {
					if (res.confirm) {
						uni.openSetting();// 打开地图权限设置
					} else if (res.cancel) {
						uni.showToast({
							title: '你拒绝了授权，无法获得周边信息',
							icon: 'none',
							duration: 1000
						})
					}
				}
			});
		},

		onShow() {
			// 动态设置标题
			// uni.setNavigationBarTitle({
			//     title: this.$t('买金'),
			// })
		   this.getAuthorizeInfo();
		},
		showPopUp() {
			uni.navigateTo({
			    url: '../../components/city/city'
			});
			// uni.showModal({
			//     title: '提示',
			//     content: '这是一个模态弹窗',
			//     success: function (res) {
			//         if (res.confirm) {
			//             console.log('用户点击确定');
			//         } else if (res.cancel) {
			//             console.log('用户点击取消');
			//         }
			//     }
			// });
		},
		link(){
			uni.navigateTo({
			    url: '../../components/map/map'
			});
		}
	}
};
